<template>
   
    <div class="d-inline">

        <div class="tree-table-content"  :class="{ 'tree-root': the_item.index < 2 }">
            <div class="unid tree-col">
                {{ the_item.unid }}
            </div>
            <div class="layer tree-col">
                {{ the_item.index }}
            </div>
            <div class="ipv tree-col">
                {{ show_pv }}
            </div>
            <div class="level tree-col">
                {{ show_vip }}
            </div>
            <div class="son_pv tree-col">
                {{ show_son_pv }}
            </div>
            <div class="can_num tree-col">
                {{ show_can_num }}
            </div>
            <div class="result tree-col  fs-15">
                {{ this.usd }}
            </div>
        </div>
    </div>
</template>

<script>
import bonus_active_layer from '@/common/es/bonus/bonusApi/bonus_active_layer'

    export default {
        name: '',
        props: {
            the_item: {
                type: Object
            },
            allow: {
                type: Boolean
            }
        },
        name: '',
        data() {
            return {
                num: 0,
                vip: '5级会员',
                usd: 0,
                ipv: 0,

            }
        },
        async mounted() {
            if (this.allow) {
                this.usd = await this.load_data()
            }
        },
        computed: {
            num_color() {
                if (this.num > 0) {
                    return true
                }
                return false
            },
            show_vip() {
                if (this.the_item.vip) { return this.the_item.vip.name }
                return ''
            },
            show_pv() {
                if (this.the_item.vip) { return this.the_item.ipv }
                return '待计算'
            },
            show_son_pv() {
                if (this.the_item.vip) { return this.the_item.son_pv }
                return ''
            },
            show_can_num() {
                if (this.the_item.vip) { return this.the_item.son_can_num }
                return ''
            },
            is_show() {
                let res = false
                if (this.the_item.vip) { res = true }
                return res
            }
        },
        methods: {
            load_data() {
                const self = this
                return new Promise((rej, rev) => {
                    let res = 0
                    
                    if (this.the_item.buys.length <= 0) {
                        this.num = 0
                        this.the_item.buys.push({
                            num: 0, pv: 120
                        })
                    } else {
                        this.num = this.the_item.buys[0].num
                    }



                    if (this.the_item.vip && this.the_item.index == 0) {
                        
                        res = bonus_active_layer(this.the_item)
                    } else {
                        // res = bonus_active_layer(this.the_item)
                    }
                    
                    // res = bonus_active_layer(this.the_item)

                    rej(res)
                })
            },
            changeNum(plus_or_minus) {

                if (plus_or_minus) {
                    this.num += 1
                } else {
                    if (this.num > 0) {
                        this.num -= 1
                    }
                }

                this.the_item.buys[0].num = this.num
            }
        }
    }
</script>

<style lang="sass" scoped>
@import "@/common/sass/table_header.sass" 
.usd
    color: #607d8b!important
</style> 


